@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
* {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Lobster', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #2d2013;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}


.container {
    display: flex;
}
label {
    display: none;
}
.search {
    display: flex;
    width: 300px;
    font-size: 16px;
    margin-right: 15px;
}

.search input {
    width: 300px;
    border: none;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    padding: 5px 3px;
    font-size: 16px;
}

.search-btn {
    width: 29px;
    line-height: 29px;
    height: 29px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border: none;
    border-left: 1px solid rgba(0, 0, 0, 0.3);
    cursor: pointer;
}

.random-btn {
    width: 29px;
    height: 29px;
    line-height: 29px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
.meal {
    width: 180px;
    height: 180px;
    position: relative;
    border: 4px solid #fff;
    cursor: pointer;
}

img {
    width: 100%;
    height: 100%;
}

.meal-info {
    background-color: rgba(0, 0, 0, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.meal:hover .meal-info {
    display: flex;
}
.meal-grid {
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
}

@media only screen and (max-width: 800px) {
    .meal-grid {
        padding: 20px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 20px;
    }
}

@media only screen and (max-width: 600px) {
    .meal-grid {
        padding: 20px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 20px;
    }
}

@media only screen and (max-width: 400px) {
    .meal-grid {
        padding: 20px;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        grid-gap: 20px;
    }
}
.meal-desc {
    margin: 30px 0;
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.meal-desc h2, h4 {
    margin-bottom: 15px;
}

.meal-desc img {
    width: 250px;
    height: auto;
    border: 4px solid #fff;
}

.desc {
    width: 70%;
    padding: 20px 30px;
    margin: 20px 0;
    border-color: #bd8532;
}

.instructions {
    width: 70%;
}
.App {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 30px auto;
  padding: 10px;
}
