.meal-desc {
    margin: 30px 0;
    text-align: justify;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.meal-desc h2, h4 {
    margin-bottom: 15px;
}

.meal-desc img {
    width: 250px;
    height: auto;
    border: 4px solid #fff;
}

.desc {
    width: 70%;
    padding: 20px 30px;
    margin: 20px 0;
    border-color: #bd8532;
}

.instructions {
    width: 70%;
}